import CONFIG from '../config'
import api from './../api/index'

const state = {
  user: {
    avatar: '',
    coins: 0,
    nick_name: '',
    open_id: '',
    player_id: ''
  },
  coin: [],
  consume: {
    data: [],
    meta: {
      pagination: {}
    }
  },
  info: {
    coin_num: 0
  },
  machine_no: '',
  hasInfinite:false
}

const mutations = {
  setUser(state, user) {
    state.user = user
  },
  setCoins(state, coin) {
    state.user.coins = state.user.coins + coin
  },
  setCoinList(state, coin) {
    state.coin = coin
  },
  setConsume(state, consume) {
    state.consume = consume
  },
  setMachineNo(state) {
    if (CONFIG.machine_no) {
      state.machine_no = CONFIG.machine_no
    }
  },
  setInfo(state, info) {
    state.info = info
  },
  changeInfinite(state,flag){
    state.hasInfinite = flag;
  }
}

const actions = {
  getUser({commit}) {
    return new Promise((success, error) => {
      api.getUser({token:CONFIG.token}).then((data) => {
          commit('setUser', data.data)
          success(data)
      })
    })
  },
  getCoinList({commit}) {
    return new Promise((success, error) => {
      api.getCoinList({
        machine_no: state.machine_no,
        token:CONFIG.token
      }).then((data) => {
          commit('setCoinList', data.data);
          success();
    })})
  },
  getConsume({commit}, page) {
    return new Promise((success, error) => {
      api.getConsume({
        page_index: page.page_index,
        page_size: page.page_size,
        token:CONFIG.token
      }).then((data) => {
          commit('setConsume', data)
          const p = data
          success(p)
      })
    })
  },
  getPayJssdk(ctx,ids) {
    return new Promise((success, error) => {
      api.getPayJssdk({
        coin_price_id: ids.coin_price_id,
        coupon_id:ids.coupon_id,
        machine_no: ctx.state.machine_no,
        token:CONFIG.token
      }).then((data) => {
          success(data)
    })
    })
  },
  getPayTpp(ctx, id) {
    return new Promise((success, error) => {
      api.getPayTpp({
        coin_price_id: id,
        machine_no: ctx.state.machine_no,
        token:CONFIG.token
      }).then((data) => {
        success(data)
      })
    })
  },
  judgeMachine(ctx) {
    return new Promise((success, error) => {
      api.judgeMachine({
        machine_no: ctx.state.machine_no,
        token:CONFIG.token
      }).then((data) => {
          ctx.commit('setInfo', data.data)
          success(data.data)
      })
    })
  },
  getFreeCoin(ctx, id) {
    return new Promise((success, error) => {
      api.getFreeCoin({
        coin_price_id:id,
        machine_no:ctx.state.machine_no,
        token:CONFIG.token
      }
      ).then((data) => {
          success(data)
      })
    })
  },
  startingDevice(ctx, num) {
    return new Promise((success, error) => {
      api.startingDevice({
        coin_num: num,
        machine_no: CONFIG.machine_no,
        token:CONFIG.token
      }).then((data) => {
          var s = ctx.state.user
          s.coins = data.data.coin_total
          s.game_ticket = data.data.game_ticket
          ctx.commit('setUser', s)
          success()
      })
    })
  },
  InfiniteGame(ctx){
    return new Promise((success,error)=>{
      api.InfiniteGame({machine_no:ctx.state.machine_no,token:CONFIG.token}).then((data)=>{
        if (data.status_code == 200) {
          ctx.commit('changeInfinite',false);
        }else {
          ctx.commit('changeInfinite',true);
        }
      }).catch((err)=>{
        ctx.commit('changeInfinite',true);
        error(err);
      })
    })
  },
  //领取优惠券
  consumer:function (id) {
    return new Promise((success,err) => {
      api.consumer({id:id,token:CONFIG.token}).then((data)=>{
        if (data.status_code == 200) {
          success(data)
        }
      })
    })
  },
  //使用优惠券
  useCoupons:function (id) {
    return new Promise(() => {
      api.useCoupons({id:id,token:CONFIG.token}).then((data) => {

      })
    })
  }
}

export default {
  state,
  mutations,
  actions
}



