<template>
  <div class="jo-index">
    <div class="header">
      <div>
        <div class="head-portrait">
          <img src="./../assets/imgs/icon_portrait.png" alt="">
        </div>
        <div class="header-main">
          <h4>当前机器号：<span>{{machine_no}}</span></h4>
          <div class="game game-coins">
            <i class="iconfont icon-jinbi"></i>
            <span>游戏币:</span><span class="coins-num">{{user.coins}}</span>
          </div>
          <div class="game game-quan" v-show="user.game_ticket>0">
            <i class="iconfont icon-quan"></i>
            <span>免费券:</span><span class="coins-num">{{user.game_ticket}}</span>
          </div>
          <div class="kefu">
            <a id="support" href="tel:0755-32904117" @click="support">
            <img src="./../assets/imgs/icon_kefu.png" alt="">
            </a>
          </div>
        </div>
      </div>
    </div>
    <div class="main">
     <div>
       <div class="activity">
         <img src="./../assets/imgs/quan_free.png" alt="" @click="useCoupons(1,$event)">
         <img src="./../assets/imgs/quan_20.png" alt="" @click="useCoupons(2,$event)">
       </div>
       <div class="center">
         <h3 @click="handleScanQRCode" id="change_device">扫码更换机器<i class="iconfont icon-go"></i></h3>
         <button class="startgame" id="coin-operated" @click="handleStartingDevice">{{start_desc}}</button>
         <!--v-if="user.coins<=0 && user.game_ticket<=0"-->
         <div class="game-num norecharge" v-if="user.coins<=0 && user.game_ticket<=0">您还没有游戏币，请先充值<span></span></div>
         <div class="game-num" v-else>
           <i id="coin_reduce" class="iconfont icon-jianhao" @click="handlerGameNum('-')"   :class="{
                        'active': gameNum === 1
                    }"></i>
            <div>
              <h3>{{gameNum * info.coin_num}}币</h3>
              <p>可玩{{gameNum}}次游戏</p>
            </div>
           <i  id="coin_add" class="iconfont icon-jiahao" @click="handlerGameNum('+')"  :class="{
                        'active': ((gameNum + 1) * info.coin_num) > (user.coins+user.game_ticket*info.coin_num)
                    }"></i>
         </div>
         <img v-if="start_desc=='投币中'" class="tbz" src="../assets/imgs/tbz.gif" alt="">
         <img v-show="is_lamp_after && start_desc=='投币启动'"  class="tbz" src="../assets/imgs/btcg.gif" alt="">
          <div class="tip">
            <p v-show="user.coins<=0 && user.game_ticket<=0">充值后点击“投币启动”按钮开始游戏</p>
            <p v-show="is_lamp_after && start_desc=='投币启动'">投币成功，<span>“摇动游戏杆”</span>即可开始游戏</p>
          </div>
       </div>
     </div>
    </div>
    <div class="footer">
      <joPay></joPay>
    </div>
  </div>
</template>

<script>
  import CONFIG from '../config'
  import {mapState} from 'vuex'
  import joPay from './wxpay'
  import MessageBox from 'mint-ui/lib/message-box'

  export default {
    data(){
      return {
        start_desc: '投币启动',
        gameNum: 1,
        maskStyle: {},
        is_start: false,
        is_lamp_after:false,
        is_recharge:false,
        loading_art: '加载更多~',
        load: false,
        max_page: false,
        maskShow:false,
        isShow:'',
        sume:[],
        recharge_auto: false,
        bgShow:false,
        scrollTop:0,
        isHasHistory:false
      }
      },
    created() {
      this.Indicator.open()
    },
    computed: mapState({
      user: state => state.user.user,
      consume: state => state.user.consume,
      machine_no: state => state.user.machine_no,
      info: state => state.user.info
    }),
    components: {
      joPay
    },
    mounted() {
      this.$store.dispatch('judgeMachine')
      this.$store.dispatch('getUser')
        .then(() => {
          this.Indicator.close()
        })
    },
    methods:{
      useCoupons(id,event){
        this.$store.dispatch('getPayJssdk',{coin_price_id:5,coupon_id:1})
          .then((data) => {
          if (typeof data == 'object'){
            alert(JSON.stringify(data))
          }else {
            alert(data)
          }
            if (data.status_code === 200) {
              const d = data.data
              wx.chooseWXPay({
                timestamp: d.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
                nonceStr: d.nonceStr, // 支付签名随机串，不长于 32 位
                package: d.package, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
                signType: d.signType, // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
                paySign: d.paySign, // 支付签名
                success: function (res) {
                  alert(JSON.stringify(res));
//                  if(type !=1){
//                    self.$store.commit('setCoins', coin)
//                  }else {
//                    self.$store.dispatch('InfiniteGame');
//                  }
//                  self.$emit('rechargeok')
//                  self.getCoinList()
                },
                fail:function (err) {
                  if(typeof err == 'object'){
                    alert(JSON.stringify(err))
                  }else {
                    alert(err)
                  }
                }
              })
            }
          }).catch((err)=>{
          if(typeof err == 'object'){
            alert(JSON.stringify(err))
          }else {
            alert(err)
          }
        })

        console.log(event.target);
//        this.$store.dispatch('consumer',id).then(()=>{
//          console.log(event.target);
//        })
      },
      handlerGameNum(x) {
        if (x === '-') {
          if (this.gameNum === 1) return false;
          this.gameNum--;
          _hmt.push(['_trackEvent', '减币', '点击', '', '']);
        } else {
          if (((this.gameNum + 1) * this.info.coin_num) > (this.user.coins+this.info.coin_num*this.user.game_ticket)) {
            if (this.user.coins < this.info.coin_num)  this.openRecharge('recharge')
            return false
          }
          this.gameNum++;
          _hmt.push(['_trackEvent', '加币', '点击', '', '']);
        }
      },
      //投币，开始游戏
      handleStartingDevice() {
        if(this.info.online == 0){
          MessageBox.alert('设备离线请扫码更换机器','提示').then(() => {
            this.handleScanQRCode();
          });
          return
        }
        if (this.is_start) {
          return
        } else {
          this.is_start = true
          this.is_lamp_after = false
          this.start_desc = '投币中'
          if (this.user.game_ticket+this.user.coins < this.info.coin_num) {
            this.start_desc='';
            this.is_start = false
            this.recharge_auto = true
            MessageBox.alert('余额不足，请充值','提示').then(() => {
              this.openRecharge('recharge')
            });
            return false
          }
          //添加百度统计
          _hmt.push(['_trackEvent', '主按钮投币', '点击', '投币-游戏次数-' + this.gameNum, '']);
          this.$store.dispatch('startingDevice', this.gameNum * this.info.coin_num)
            .then(() => {
              this.is_lamp_after = true
              this.is_start = false
              this.start_desc = '投币启动'
              this.gameNum = 1;
              this.isHasHistory = true;
              setTimeout(() => {
                this.is_lamp_after = false
              },12000*this.gameNum)
            })
            .catch(() => {
              this.is_start = false
              this.start_desc = '投币启动'
            })
        }
      },
      handleScanQRCode() {
        _hmt.push(['_trackEvent', '扫码更换机器', '点击', '', '']);//添加百度统计
        if (CONFIG.isAlipay) {
          ap.scan(function (res) {
            window.location.href = res.code
          })
        } else {
          wx.scanQRCode({
            needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
              window.location.href = result
            },
            error: function () {
              alert('扫码失败')
            }
          })
        }
      },
      support(){
        _hmt.push(['_trackEvent', '客服', '点击', '', '']);
      },
    }
 }
</script>

<style scoped>
  .jo-index {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0 0.22rem;
    background: url("./../assets/imgs/bg.png");
    background-size: 100% 100%;
  }
  *{
    box-sizing: border-box;
  }
  .header{
    width: 100%;
    /*height: 1.58rem;*/
    height: 12%;
    /*background: red;*/
    position: relative;
  }
  .header>div{
    width: 100%;
    position: absolute;
    top:50%;
    transform: translateY(-50%);
  }
  .header .head-portrait{
    width: 1.12rem;
    height: 1.12rem;
    background: #feccbc;
    border-radius: 50%;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
  }
  .header .head-portrait img{
    width: 80%;
    max-height: 80%;
  }
  .header .header-main{
    width: 100%;
    padding:0 0 0 1rem;
    position: absolute;
    left: 0;
    top:0;
  }
  .header .header-main h4{
    font-size: 0.24rem;
    color: #ffe7e0;
    line-height: 0.44rem;
    /*margin: 0.04rem 0 0 0;*/
    text-indent: 0.15rem;
  }
  .header .header-main .game{
    height: 0.62rem;
    padding: 0 0.24rem 0 0.14rem;
    line-height: 0.62rem;
    border-radius: 0.31rem;
    background:#ffe7e0;
    float: left;
  }
  .header .header-main .game i{
    font-size: 0.4rem;
    color: #fe5f5b;
    font-weight: 500;
    text-align: center;
  }
  .header .header-main .game span{
    font-size: 0.28rem;
    color: #fe5f5b;
    display: inline-block;
    vertical-align: top;
  }
  .header .header-main .game span.coins-num{
    font-size: 0.36rem;
    color: #fe5f5b;
    font-weight: 600;
    margin: 0 0 0 0.1rem;
  }
  .header .header-main .game-quan{
    margin: 0 0 0 0.2rem;
  }
  .header .header-main .kefu{
    width: 0.62rem;
    height: 0.62rem;
    background: #ffe7e0;
    border-radius: 50%;
    float: right;
    position: relative;
  }
  .header .header-main .kefu a{
    display: block;
    width: 100%;
    height: 100%;
  }
  .header .header-main .kefu img{
    width: 80%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    margin: 0 0.22rem 0 0;
  }
  .main{
    width: 100%;
    height: 60%;
    /*height: calc(100% - 4.98rem);*/
    position: relative;
  }
  .main .activity{
    width: 100%;
    position: relative;
    overflow: hidden;
  }
  .main>div{
    width: 100%;
    position: absolute;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
  }
  .main .activity img{
    width: 48.5%;
    min-height: 1.8rem;
    display: block;
    float: left;
  }
  .main .activity img:last-child{
    float: right;
  }
  .main .center{
    width: 100%;
    /*height: 5rem;*/
    background: #fff;
    margin: 0.32rem 0 0 0;
    padding: 0.28rem 0.2rem 0 0.2rem;
    border-radius: 10px;
    position: relative;
  }
  .main .center .tbz{
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top:0;
  }
  .main .center h3{
    font-size: 0.32rem;
    color: #fd643b;
    line-height: 0.32rem;
    margin: 0 0 0.2rem 0;
  }
  .main .center .startgame{
    width: 3.06rem;
    height: 3.06rem;
    background: url("./../assets/imgs/press_begin.png");
    background-size: 100% 100%;
    border: none;
    outline: none;
    font-size: 0.5rem;
    color: #fff;
    display: block;
    margin: 0 auto;
  }

  .main .center .game-num{
    width: 4.18rem;
    height: 0.93rem;
    border: 1px solid #ff784e;
    border-radius: 0.18rem;
    font-size: 0.24rem;
    line-height: 0.91rem;
    margin: -0.63rem auto 0 auto;
    background: #fff;
    color: #7e7e7e;
    text-align: center;
  }
  .main .center .game-num.norecharge{
    border: 1px solid #afafaf;
  }

  .main .center .game-num i.active {
    color: #c8c8c8;
  }
  .main .center .game-num i{
    width: 0.81rem;
    float: left;
    font-size: 0.5rem;
    color: #ff784e;
    text-align: center;
    height: 0.67rem;
    line-height: 0.67rem;
    border-right: 1px solid #ff784e;
    margin: 0.1rem 0;
  }
  .main .center .game-num i:last-of-type{
    border-right: none;
    border-left: 1px solid #ff784e;
  }
  .main .center .game-num>div{
    width: calc(100% - 1.62rem);
    float: left;
    text-align: center;
  }
  .main .center .game-num>div h3{
    font-size: 0.42rem;
    color: #ff784e;
    font-weight: 600;
    line-height: 0.42rem;
    margin: 0.06rem 0 0.08rem 0;
  }
  .main .center .game-num>div p{
    font-size: 0.24rem;
    color: #7e7e7e;
    line-height: 0.24rem;
  }
  .main .center .tip{
    height: 0.84rem;
  }
  .main .center .tip>p{
    font-size: 0.24rem;
    color: #7e7e7e;
    line-height: 0.64rem;
    text-align: center;
  }
  .main .center .tip>p>span{
    font-weight: 600;
    color: black;
  }

  .footer{
    width: 100%;
    /*height: 3.4rem;*/
    /*height: 28%;*/
    height: 28%;
    min-height: 3.2rem;
    position: absolute;
    left: 0;
    bottom: 0;
    /*padding: 0.36rem 0.22rem 0 0.22rem;*/
    overflow: hidden;
  }
</style>



